conjugationForms.sort(function(a, b) { return b.forms[0].length - a.forms[0].length; });

const verbTypes = ["v5u", "v5k", "v5k-s", "v5g", "v5s", "v5t", "v5m", "v5b", "v5n", "v5r", "v1"];
const verbEndings = ["", "", "", "", "", "", "", "", "", "", ""];
const verbEndingsTypes = ["u", "k", "k-s", "g", "s", "t", "m", "b", "n", "r"];


const destep = function (word) {
    const found = [];
    let i;
    const last = conjugationForms.length;
    let form;
    let j;
    const llast = verbEndings.length;
    let suffix;
    for (i = 0; i < last; i++) {
        form = conjugationForms[i];
        for (j = 0; j < llast; j++) {
            suffix = form.forms[j];
            if (!suffix.trim()) continue;
            if (word.indexOf(suffix) > -1 && word.indexOf(suffix) + suffix.length == word.length) {
                word = word.replace(suffix, verbEndings[j]);
                found.push(form.name);
                return {
                    word: word,
                    found: found
                };
            }
        }
    }
    return {
        word: word,
        found: []
    };
};

module.exports = {

    /**
     * conjugate a verb
     */
    conjugate: function (verb, type) {
        if (type == "v5r-i") // TODO: FIX THIS
            type = "v5r";
        let index;
        const ret = [];
        const lastchar = verb.substring(verb.length - 1, verb.length);
        if (!type) {
            index = verbEndings.indexOf(lastchar);
        }else{
            let t = type.toLowerCase();
            if (type !== 'v1'){
                for (let j = 0; j < verbEndings.length ; j++) {
                    if (lastchar === verbEndings[j]){
                        t += verbEndingsTypes[j];
                        break;
                    }
                }
            }
            //console.log(t);
            index = verbTypes.indexOf(t);
            if (index == -1) {
                console.error(type + " not found");
            }
        }
        const verbstem = verb.substring(0, verb.length - 1);
        let i;
        const e = conjugationForms.length;
        let form, specific;
        for (i = 0; i < e; i++) {
            form = conjugationForms[i];
            specific = form.forms[index];
            if (specific !== false) {
                ret.push({name: form.name, form: verbstem + specific});
            }
        }
        ret.getForm = function(form){
            for (let i = 0; i < ret.length; i++) {
                const entry = ret[i];
                if (entry.name == form) return entry.form;
            }
            console.log(form +"not found");
            console.log(ret);
        };
        return ret;
    },

    /**
     * try to find the original verb for a conjugation
     */
    unconjugate: function (word) {
        let found = [];
        let step = {word: word};
        do {
            step = destep(step.word);
            found = step.found.concat(found);
        } while (step.found.length > 0);
        return {
            word: step.word,
            forms: found
        };
    }

};

